.promo {
  width: 490px;
  height: 355px;
  padding-top: 164px;
  transition: transform 0.5s;

  &__heading {
    margin-bottom: 32px;
    font-family: Poppins, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 64px;
    line-height: 68px;
    letter-spacing: -2px;
    color: #fff;
  }
  &__text {
    margin-bottom: 83px;
    font-family: "Open Sans", sans-serif ;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: #fff;
  }
  &__button {
    width: 226px;
    height: 50px;
    background-color: #2060f6;
    border-radius: 8px;
    color: #fff;
    font-family: Poppins, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 15px;
    line-height: 22px;
    border-style: none;
    transition: background-color 0.3s;
    &:hover {
      background-color: rgba($color: #3090ff, $alpha: 0.5);
      box-shadow: 0 6px 23px  #fff;
    }
  }
}

@media screen and (max-width: $small-screen-breakpoint) {
  .promo {
    padding-top: 580px;
  }
}

@media screen and (max-width: $smallest-screen-breakpoint) {
  .promo {
    margin: 0 auto;
    width: 280px;
    padding-top: 400px;
    &__heading {
      font-size: 48px;
    }
    &__text {
      font-size: 16px;
    }
  }
}
